














import Vue from 'vue'

export default Vue.extend({
	name: 'Block',

	components: {
	},

	props: {
		title: {
			type: String,
			default: '',
			required: false,
		},

		order: {
			type: Number,
			default: 0,
			required: true,
		}
	},

	data: () => ({
		active: false,
	}),

	computed: {
		cardN() {
			const isActive = (this.active as boolean)
			const offset: number = isActive ? 0 : 18
			return {
				top: `${(this.order as number) * offset}vmin`,
				zIndex: isActive ? 1000 : (this.order as number)
			}
		}
	},

	methods: {
		toggleActive() {
			this.active = !this.active
		}
	}
})
